.version-label {
    position: absolute;
    bottom: 0px;
    left: 5px;
    font-size: .8em;
    opacity: 0.5;
    z-index: 1000;
    color: var(--ion-color-medium);
}

.dashboard {
    height: 100%;

    &-header {
        &-toolbar {
            ion-icon {
                height: 40px;
                width: 50px;
            }

            &-battery-icon {
                height: 100%;
                width: 50px;
                text-align: center;

                .battery {
                    &-container {
                        fill: var(--ion-color-primary-contrast);
                    }

                    &-gauge {
                        fill: var(--ion-color-primary-tint);
                    }

                    &-button {
                        fill: #ccc;
                    }

                    &-text {
                        fill: var(--ion-color-medium-shade);
                        font-size: 11px;
                    }
                }
            }

            &-logo {
                height: 40px;
                margin: 10px;
            }
        }
    }

    &-content {
        &-list {
            padding-bottom: 20px;
            height: 100%;
            display: flex;
            flex-direction: column;
            align-content: center;

            &-spacer {
                flex: 1;
            }

            .segment-button-checked {
                --background-hover: var(--ion-color-primary);
            }

            &-footer1 {
                height: 60px;
                min-height: 60px;
                padding-bottom: 9px;
                ion-segment-button {
                    padding: 5px;
                    min-height: 60px;
                    border: 1px solid var(--ion-color-primary);
                }
                ion-segment {
                    --color-checked: var(--ion-color-primary-contrast);
                    --background-checked: var(--ion-color-primary);
                    ion-segment-button:first-child {
                        border-radius: 5px 0px 0px 0px;
                    }
                    ion-segment-button:last-child {
                        border-radius: 0px 5px 0px 0px;
                    }    
                }
            }

            &-footer2 {
                height: 60px;
                min-height: 60px;
                padding-bottom: 30px;
                ion-segment-button {
                    height: 60px;
                    padding: 5px;
                    border: 1px solid var(--ion-color-primary);
                }
                ion-segment {
                    --color-checked: var(--ion-color-primary-contrast);
                    --background-checked: var(--ion-color-primary);
                    ion-segment-button:first-child {
                        border-radius: 0px 0px 0px 5px;
                    }
                    ion-segment-button:last-child {
                        border-radius: 0px 0px 5px 0px;
                    }    
                }
            }

            &-header {
                color: var(--ion-color-medium);
                padding: 0px;

                ion-label {
                    margin: auto;
                }
            }

            &-item {
                height: 100%;
                display: flex;
                background: url("../../assets/mv1_bg.png") no-repeat center;
                background-size:  contain;
    
                ion-button {
                    --background: var(--ion-color-medium-shade);
                    margin: 10px;
                    height: 36px;
                    width: 32px;
                    margin-left: 3px;
                    margin-right: 3px;
                    @media screen and (min-height: 450px) {                        
                        margin-bottom: 66px;
                    }
                }

                .profile-dots, .profile-text {
                    display: none;                    
                }
                .profile-svg {
                    width: 150px;
                    height: 90px;
                    margin: auto;                     
                }
                @media screen and (min-height: 450px) {
                    .profile-dots, .profile-text {
                        display: flex;
                    }
                    .profile-svg {
                        height: 150px;
                    }
                }
            }
        }
    }
}

.spacer {
    flex: 1;
}
.units-group {
    position: absolute;
    bottom: 145px;
    left: 18px;
    font-size: .8em;
    z-index: 10;
    color: var(--ion-color-medium-shade);
    display: flex;
    align-items: center;
    ion-label {
        padding-top: 5px;
    }
}